import React from 'react';
import { Helmet } from 'react-helmet';

export default class Application extends React.Component {
    render() {
        return (
            <div className='application'>
                <Helmet>
                    <title>DYL Editor</title>
                    <meta
                        name='title'
                        content='DYL Editor - Edit Video And Audio'
                    />
                    <meta
                        name='description'
                        content='DYL Editor is a photo and video editing website under DYL Download Facebook Video Extension'
                    />

                    <meta property='og:type' content='website' />
                    <meta property='og:url' content='https://github.com/dim0147/dyl-editor' />
                    <meta
                        property='og:title'
                        content='DYL Editor - Edit Video And Audio'
                    />
                    <meta
                        property='og:description'
                        content='DYL Editor is a photo and video editing website under DYL Download Facebook Video Extension'
                    />
                    <meta
                        property='og:image'
                        content='https://github.com/dim0147/dyl-editor/android-chrome-512x512.png'
                    />

                    <meta
                        property='twitter:card'
                        content='summary_large_image'
                    />
                    <meta
                        property='twitter:url'
                        content='https://github.com/dim0147/dyl-editor'
                    />
                    <meta
                        property='twitter:title'
                        content='DYL Editor - Edit Video And Audio'
                    />
                    <meta
                        property='twitter:description'
                        content='DYL Editor is a photo and video editing website under DYL Download Facebook Video Extension'
                    />
                    <meta
                        property='twitter:image'
                        content='https://github.com/dim0147/dyl-editor/android-chrome-512x512.png'
                    />
                </Helmet>
            </div>
        );
    }
}
